*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    /*font-family: 'Rowdies', cursive;*/
}

$azul: rgb(0, 27, 150);
$amarelo: yellow;

html,body{
    height: 100%;
}

.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1100px;
    margin: 0 auto;
    background: $azul;
}

.nav{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    font-size: 20px;
    background: $amarelo;
    text-shadow: 1px 1px 1px black;
    font-weight: 900;
}

.nav .logo-menu{
    background-image: url('../imagens/LOGO\ OFICIAL\ ATUALIZADA.png');
    width: 100px;
    height: 100px;
    background-size: 100% 100%;
}

.nav-desktop ul li{
    display: inline-block;
    padding-left: 20px;
}

.nav-desktop ul li a{
    text-decoration: none;
    padding: 20px 0;
    color: white;
}


.nav-desktop ul li a:hover{
    transition: 0,5s;
}




