.modal-bg{
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.5);
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.5s;
}

.bg-active{
    visibility: visible;
    opacity: 1;
}

.modal{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 500px;
    max-width: 80%;
    background-color: white;
    border: 5px solid rgb(255, 234, 0);
}

.modal h2{
    text-align: center;
    margin: 20px;
}

.modal-close{
    width: 150px;
    padding: 10px;
    margin: 10px;
    border: none;
    cursor: pointer;
    color: white;
    background: rgb(0, 27, 150); 
}

.modal-close:hover{
    background: red;
 }








.section1{
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90%;
    /*background: red;*/
}

.section1 h2{
    text-align: center;
    margin: 40px 0;
    font-size: 30px;
}


.section1 .slider{
    position: relative;
    width: 90%;
    max-width: 500px;
    height: 400px;
}

.slider img{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    transition: opacity 800ms;
}

.slider img.selected{
    opacity: 1;
}






.section2{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 40px;
    /*background: green;*/
}

.section2 h2{
    text-align: center;
    font-size: 30px;
}

.section2 .areas{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.section2 .ml{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    /*background: #777;*/
}

.section2 .ml .ml-logo{
    background-image: url('../imagens/mercadolivrelogo.jpg');
    width: 100px;
    height: 100px;
    background-size: 100% 100%;
    margin: 20px;


   /* width: 100px;
    height: 50px;
    
    background: blue;*/
}

.section2 .ml .ml-reputation{
    background-image: url('../imagens/reputacao.PNG');
    max-width: 90%;
    width: 500px;
    height: 500px;
    background-size: 100% 100%;
   /* width: 300px;
    height: 300px;
    background: red;*/
}




.section3{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    /*background: #ccc;*/
}

.section3 h2{
    font-size: 30px;
}

.section3 .benefits{
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     width: 70%;
     margin: 40px 0;
}

.section3 .benefits span{
    font-family: 'Rowdies', cursive;
    font-size: 20px;
    color: #ffff00;
}

.section3 .benefits  p{
    width: 50%;
    margin-top: 10px;
    line-height: 30px;
    color: white;
}

.section3 .benefits .benefits-details{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 20px;
}

.section3 .benefits .post-time-icon{
    background-image: url('../imagens/clock.png');
    width: 100px;
    height: 100px;
    background-size: 100% 100%;
    margin: 30px;
}

.section3 .benefits .safety-icon{
    background-image: url('../imagens/box.png');
    width: 100px;
    height: 100px;
    background-size: 100% 100%;
    margin: 30px;
}

.section3 .benefits .support-icon{
    background-image: url('../imagens/support.png');
    width: 100px;
    height: 100px;
    background-size: 100% 100%;
    margin: 30px 0 0 0;
}
 







.section4{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background: rgb(255, 234, 0);
}

.section4 h3{
    text-align: center;
    padding-top: 20px;
}

.section4 .form{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding-top: 20px;
}

.section4 .form input{
    width: 500px;
    margin: 10px;
    padding: 10px;
}

.section4 .form textarea{
    width: 500px;
    padding: 10px;
    margin: 10px;
    resize: none;
}

.section4 .form button{
    width: 100px;
    padding: 10px;
    margin: 10px;
    border: none;
    cursor: pointer;
    color: white;
    background: rgb(0, 27, 150); 
}

.section4 .form button:hover{
   background: red;
}

.section4 h1{
    margin: 20px;
}

.section4 .contacts{
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: 20px;
}

.section4 .contacts .whatsapp-logo{
    background-image: url('../imagens/whatsapp.png');
    width: 80px;
    height: 80px;
    background-size: 100% 100%;
}

.section4 .email-logo{
    background-image: url('../imagens/gmail.png');
    width: 80px;
    height: 80px;
    background-size: 100% 100%;
    cursor: pointer;
}





footer{
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 10px 0;
    background: rgb(0, 27, 150);
}

footer span{
    font-size: 15px;
    font-style: italic;
}



@media(max-width: 800px){
    .modal{
        justify-content: center;
        width: 500px;
        max-width: 80%;
    }
}



@media(max-width: 600px){

    .nav{
        display: flex;
        flex-direction: column;
    }

    .nav .logo-menu{
        margin-bottom: 10px;
    }

    .nav-desktop ul li{
        display: block;
        text-align: center;
        padding: 10px 0;
    }

    .nav-desktop ul li a{
        padding: 20px;
    }

    .section3 .benefits  p{
        width: 100%;
    }
    
    .section4 .form input{
        width: 300px;
    }
    
    .section4 .form textarea{
        width: 300px;
    }

    .section4 .form input:hover{
        width: 70%;
        margin: 10px;
        padding: 10px;
        transition: 0.3s;
    }

    .section4 .form textarea:hover{
        width: 70%;
        margin: 10px;
        padding: 10px;
        transition: 0.3s;
    }

 
}


@media(max-width: 400px){

    .section3 .benefits{
        width: 90%;
   }
    
    .section4 .form input{
        width: 200px;
    }
    
    .section4 .form textarea{
        width: 200px;
    }
 
}
